/* 폰트 정의 */
@font-face {
    font-family: 'HoltwoodOneSC';
    src: url('../font/HoltwoodOneSC-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NanumGothic';
    src: url('../font/NanumGothic.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

/* 기본 초기화 및 배경색 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#profile_wrapper {
    width: 100vw;
    height: 970px;
    background-color: #F5F5F5;
}