/* src/css/Main.css */

@font-face {
    font-family: 'HoltwoodOneSC';
    src: url('../font/HoltwoodOneSC-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NanumGothic';
    src: url('../font/NanumGothic.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

/* 기본 초기화 및 배경색 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

#main-wrapper {
    width: 100vw;
    height: 970px;
    background-color: #FFF9E6;
}


/* 제목 스타일 */
#title {
    font-family: 'HoltwoodOneSC', sans-serif;
    font-size: 128px;
    color: #fff;
    text-align: center;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

/* 프로필 이미지 박스 */
#profileimg {
    width: 555px;
    height: 600px;
    background-color: #FFF9E6;
    margin: 0 auto;
}

#profileimg img {
    width: 600px;
    height: 740px;
    object-fit: cover;
}

/* 소개글 */
#intro {
    font-family: 'NanumGothic', sans-serif;
    font-weight: normal;
    font-size: 40px;
    color: #60493A;
}
